html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

.bird {
  position: relative;
  width: 200px;
  height: 200px;
  margin: 100px auto;
}

.forelock {
  width: 75px;
  height: 20px;
  background: red;
  border-radius: 13px;
  border: 1px solid #000;
  left: 30%;
  top: -15px;
  position: absolute;
}

.forelock__frontside {
  transform: rotate(-130deg);
}

.forelock__backside {
  transform: rotate(-170deg);
  top: -10px;
  left: 20%;
}

.shell {
  background: red;
  width: 100%;
  height: 100%;
  border: 1px solid #000;
  border-radius: 50%;
  z-index: 1;
  position: relative;
}

.tail {
  width: 50px;
  height: 10px;
  background: #000;
  position: absolute;
}

.tail__wrap {
  left: -15px;
  bottom: 35%;
  position: relative;
}

.tail__1 {
  transform: rotate(40deg);
}

.tail__3 {
  transform: rotate(-40deg);
}

.eyebrow {
  width: 80px;
  height: 20px;
  background: #000;
  position: absolute;
  transform: rotate(10deg);
  top: 60px;
  left: 22.5px;
}

.eyebrow__wrap {
  position: relative;
}

.eyebrow-right {
  transform: rotate(-10deg);
  left: 97.5px;
}

.eye {
  width: 50px;
  height: 50px;
  background: #fff;
  border-radius: 50%;
  position: absolute;
  top: 72.5px;
  left: 48px;
}

.eye__wrap {
  position: relative;
}

.eye-right {
  left: 98px;
}

.eye__inside {
  position: absolute;
  top: 50%;
  left: 60%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  background: #000;
  border-radius: 50%;
}

.eye__inside-right {
  left: 40%;
}

.beak {
  width: 50px;
  height: 50px;
  background: #d5972a;
  transform: rotate(45deg);
  position: absolute;
  top: 110px;
  left: 73.5px;
  overflow: hidden;
}

.beak:before {
  content: "";
  position: fixed;
  height: 1px;
  width: 90px;
  background: #000;
  top: 6%;
  left: 1%;
  right: 1px;
  transform: rotate(-45deg);
}
